*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  color: inherit;
  font: inherit;
  line-height: 1;
}

html {
  hanging-punctuation: first last;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

div,
p,
li {
  text-wrap: pretty;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}

img, picture, video {
  display: block;
  width: 100%;
  height: auto;
  vertical-align: middle;
  font-style: italic;
  background-repeat: no-repeat;
  background-size: cover;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    scroll-behavior: smooth;
    scroll-padding-top: 1rem;
  }
}
.content-grid {
  --breakout-size: calc((var(--template-breakout-max-width) - var(--template-max-width)) / 2);
  display: grid;
  grid-template-columns: [full-width-start] minmax(var(--template-padding), 1fr) [content-start] min(100% - var(--template-padding) * 2, var(--template-max-width) - var(--template-padding) * 2) [content-end] minmax(0, var(--breakout-size)) [breakout-end] minmax(var(--template-padding), 1fr) [full-width-end];
}

.content-grid > :not(.breakout, .full-width),
.full-width > :not(.breakout, .full-width) {
  grid-column: content;
}

.content-grid > .breakout {
  grid-column: breakout;
}

.content-grid > .full-width {
  grid-column: full-width;
  display: grid;
  grid-template-columns: inherit;
}

:root {
  --template-padding: 48px;
  --template-max-width: 1440px;
  --template-breakout-max-width: var(--template-max-width);
  --text-black: #1D232C;
  --text-blue: #254375;
  --text-accent: #D08935;
  --text-grey: #73777A;
  --bg-black: #1D232C;
  --bg-blue: #254375;
  --bg-light-blue: #D5DFE9;
  --border-radius: 24px 0px 0px 0px;
}

body {
  font-family: Inter;
  background-color: #fff;
  padding-bottom: 100vh;
}

main {
  position: relative;
  z-index: 1;
  background-color: #fff;
}

h1 {
  font-size: 5rem;
  font-weight: bold;
}

.link {
  text-decoration: none !important;
  line-height: 1.3;
  background-image: linear-gradient(90deg, currentColor 50%, transparent 50%);
  background-size: 200% 1px;
  background-position: right bottom 0.3rem;
  background-repeat: no-repeat;
  padding-block-end: 0.25em;
  transition: color 300ms, background-position 300ms;
}
.link:hover, .link:focus {
  background-position: left bottom 0.3rem;
}

.form-row {
  margin-bottom: 32px;
}

.input-title {
  font-size: 1.125rem;
  line-height: 1.3;
  margin-bottom: 12px;
}
.input-title__required {
  color: var(--text-accent);
  font-size: 1rem;
  margin-left: 8px;
}

.input {
  display: block;
  width: 100%;
  border: 1px solid var(--bg-light-blue);
  padding: 20px 16px 19px;
  border-radius: 24px 0px 0px 0px;
}
.input:focus {
  box-shadow: inset 0 0 2px var(--bg-blue);
}
.input.is-error {
  box-shadow: inset 0 0 1px #E81A0C;
}

.btn {
  position: relative;
  overflow: hidden;
  background: none;
  border: 1px solid var(--bg-light-blue);
  font-size: 1rem;
  line-height: 1.2;
  font-weight: bold;
  color: #fff;
  display: flex;
  flex-shrink: 0;
  gap: 24px;
  justify-content: space-between;
  padding: 0 24px 0 24px;
  height: 52px;
  align-items: center;
  border-radius: 24px;
  transition: all 0.3s;
}
.btn span {
  z-index: 1;
  white-space: nowrap;
}
.btn:hover {
  color: var(--text-blue);
}
.btn:hover .btn__arrow {
  background-color: var(--bg-blue);
}
.btn:hover .btn__arrow svg {
  stroke: #fff;
}
.btn::after {
  border-radius: 0;
  background-color: #fff;
  content: "";
  position: absolute;
  z-index: 0;
  transition: all 0.3s;
  left: 0;
  top: 0;
  width: 0;
  height: 100%;
}
.btn:hover:after {
  width: 100%;
}
.btn__arrow {
  border-radius: 32px;
  width: 32px;
  height: 32px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}
.btn__arrow svg {
  stroke: var(--text-blue);
  width: 24px;
  height: 24px;
}

.btn-arrow {
  padding-right: 16px;
}

.btn-white {
  background-color: #fff;
  color: var(--black);
}
.btn-white::after {
  background-color: var(--bg-blue);
}
.btn-white:hover {
  border-color: var(--bg-blue);
  color: #fff;
}
.btn-white:hover .icon-svg,
.btn-white:hover .btn__arrow {
  stroke: #fff;
  color: #fff;
}

.btn-light-blue {
  background-color: var(--bg-light-blue);
  color: var(--black);
}
.btn-light-blue::after {
  background-color: var(--bg-blue);
}
.btn-light-blue:hover {
  border-color: var(--bg-blue);
  color: #fff;
}
.btn-light-blue:hover .icon-svg,
.btn-light-blue:hover .btn__arrow {
  stroke: #fff;
  color: #fff;
}

.btn-blue {
  background-color: var(--bg-blue);
  color: #fff;
}
.btn-blue .btn__arrow {
  stroke: var(--text-blue);
}
.btn-blue:hover {
  color: var(--black);
}
.btn-blue:hover .btn__arrow {
  background-color: var(--bg-blue);
  stroke: #fff;
}
.btn-blue::after {
  background-color: #fff;
}

.btn-language {
  padding-left: 24px;
  padding-right: 24px;
  font-weight: normal;
}

.btn-header {
  width: 100px;
  height: 52px;
  background-color: #fff;
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.btn-header__line {
  width: 24px;
  height: 1px;
  background-color: var(--bg-blue);
  margin: 3px 0;
  transition: 0.2s ease-in-out;
  position: relative;
}

.title {
  font-size: 3rem;
  font-weight: bold;
  padding-bottom: 32px;
  max-width: 30ch;
}
.title__border {
  width: 100%;
  border-bottom: 1px solid var(--bg, #D5DFE9);
  margin-bottom: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.subtitle {
  font-size: 1.125rem;
  font-weight: bold;
  margin-bottom: 24px;
}
.subtitle__icon {
  color: var(--text-blue);
  width: 24px;
  height: 24px;
  vertical-align: sub;
  margin-right: 12px;
}

.border-radius {
  border-radius: 24px 0 0 0;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  padding-top: 40px;
  transition: background 0.1s;
}
.header__main {
  display: flex;
  justify-content: space-between;
}
.header__main-right, .header__main-left {
  display: flex;
  align-items: center;
  flex-shrink: 0;
  gap: 16px;
  width: 400px;
}
.header__main-right {
  justify-content: flex-end;
}
.header__contacts {
  border-left: 1px solid rgba(255, 255, 255, 0.25);
  padding: 5px 16px;
  display: flex;
  flex-direction: column;
}
.header__contacts-link {
  line-height: 1.2;
}
.header__logo {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 128px;
  padding-right: 16px;
}
.header__logo-picture {
  background: url("./../images/logo-white.svg") no-repeat center center/contain;
  width: 100%;
  height: 48px;
}
.header__expanded {
  display: none;
  padding-top: 100px;
}
.header__expanded-nav {
  display: flex;
  justify-content: space-between;
  gap: 140px;
}
.header__nav-address {
  background-color: var(--bg-light-blue);
  padding: 24px;
}
.header__nav-address-title {
  font-size: 0.875rem;
  color: var(--text-grey);
  margin-bottom: 12px;
  line-height: 1.3;
}
.header__nav-address p {
  font-size: 1.125rem;
  margin-bottom: 48px;
  line-height: 1.3;
}
.header__nav-address p:last-child {
  margin-bottom: 0;
}
.header__nav-column {
  display: flex;
  flex-direction: column;
  gap: 12px;
  min-width: 318px;
}
.header__nav-column:last-child {
  grid-column: 1/3;
}
.header__nav-title {
  font-size: 0.875rem;
  color: var(--text-grey);
  padding-bottom: 16px;
  margin-bottom: 4px;
  border-bottom: 1px solid var(--bg-light-blue);
}
.header__nav-link {
  font-size: 2rem;
  font-weight: bold;
  line-height: 1.3;
  align-self: flex-start;
  background-size: 200% 2px;
}
.header__copyright-wrapper {
  font-size: 0.875rem;
  line-height: 1.3;
  color: var(--text-grey);
  padding-top: 24px;
  margin-top: 80px;
  border-top: 1px solid var(--bg-light-blue);
}
.header.is-fixed {
  position: fixed;
  background-color: #fff;
  color: var(--text-black);
  z-index: 3;
  padding-bottom: 24px;
}
.header.is-fixed .header__expanded {
  display: block;
}
.header.is-fixed .header__logo-picture {
  background-image: url("./../images/logo-blue.svg");
}
.header.is-fixed .btn-header {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn-header__line {
  background-color: #fff;
}
.header.is-fixed .btn-header__line:first-child {
  transform: rotate(45deg);
  margin-top: 3px;
}
.header.is-fixed .btn-header__line:last-child {
  transform: rotate(-45deg);
  margin-top: -3px;
}
.header.is-fixed .btn {
  background: #fff;
  border: 1px solid var(--bg-light-blue);
  color: var(--text-black);
}
.header.is-fixed .btn__arrow {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn__arrow svg {
  stroke: #fff;
}
.header.is-fixed .btn:hover:after {
  background-color: var(--bg-blue);
}
.header.is-fixed .btn:hover {
  color: #fff;
  border-color: var(--bg-light-blue);
}
.header.is-fixed .btn:hover .btn__arrow {
  background-color: #fff;
}
.header.is-fixed .btn:hover .btn__arrow svg {
  stroke: var(--text-blue);
}

.orbit {
  position: absolute;
  width: 1000px;
  height: 1000px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.25);
}

.planet {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
  top: calc(50% - 4px);
  left: calc(50% - 4px);
  animation: planet 10s linear infinite;
}

@keyframes planet {
  0% {
    transform: rotate(0deg) translateX(-500px) rotate(0deg);
  }
  100% {
    transform: rotate(360deg) translateX(-500px) rotate(-360deg);
  }
}
.section {
  padding-top: 140px;
  padding-bottom: 140px;
}
.section__wrapper {
  display: flex;
  flex-direction: column;
  align-items: start;
}

.section-main {
  height: 100vh;
  height: 100svh;
  background: url(./../images/main.jpg) center center/cover;
  color: #fff;
  overflow: hidden;
  position: relative;
  padding-top: 92px;
  padding-bottom: var(--template-padding);
}
.section-main .orbit-left {
  left: -300px;
  bottom: -400px;
}
.section-main .orbit-right {
  right: -320px;
  top: -350px;
}
.section-main .section__wrapper {
  z-index: 1;
  position: relative;
}
.section-main::before {
  display: block;
  content: "";
  background: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  position: absolute;
}
.section-main::after {
  display: block;
  content: "";
  background: linear-gradient(0deg, rgba(29, 35, 44, 0) 0%, #1D232C 100%);
  opacity: 0.5;
  width: 100%;
  height: 200px;
  left: 0;
  right: 0;
  position: absolute;
}
.section-main__title-container {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 24px;
  justify-content: center;
}
.section-main__list {
  font-size: 1.3125rem;
}
.section-main__list-item {
  line-height: 1.3;
  margin-bottom: 12px;
}
.section-main__list-item-arrow {
  width: 24px;
  height: 24px;
  stroke: #fff;
  vertical-align: sub;
  margin-right: 12px;
  display: inline-block;
}

.stats-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1px;
}
.stats-cards__card {
  max-width: 500px;
  background-color: var(--bg-blue);
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.stats-cards__card:first-child {
  border-top-left-radius: 24px;
}
.stats-cards__card:last-child {
  border-top-right-radius: 24px;
}
.stats-cards__header {
  font-size: 4.25rem;
  font-weight: bold;
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  padding-bottom: 16px;
  margin-bottom: 16px;
}
.stats-cards__body {
  line-height: 1.3;
}

.section-projects .project {
  flex: 0 0 318px;
}

.projects {
  display: flex;
  gap: 24px;
  overflow: hidden;
}

.slider-buttons {
  display: flex;
  gap: 12px;
}
.slider-buttons__prev::after {
  left: unset;
  right: 0;
}
.slider-buttons__prev svg {
  transform: rotate(180deg);
}
.slider-buttons > * {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  border: none;
}
.slider-buttons > * svg {
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}

.projects-slider {
  width: 100%;
}

.project {
  position: relative;
}
.project__image-wrapper {
  margin-bottom: 24px;
  position: relative;
}
.project__image {
  width: 100%;
}
.project__goto-btn {
  position: absolute;
  right: 24px;
  bottom: 24px;
  border-radius: 24px;
  display: flex;
  align-items: center;
  height: 52px;
  padding: 0px 24px;
  background: #fff;
  border: none;
}
.project__goto-btn .icon-svg {
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}
.project__title {
  font-size: 1.3125rem;
  font-weight: bold;
  line-height: 1.3;
  margin-bottom: 18px;
}
.project__title .link {
  background-size: 200% 2px;
}
.project__link {
  display: inline-block;
}
.project__link:hover .btn {
  border-color: var(--bg-blue);
}
.project__link:hover .btn::after {
  width: 100%;
}
.project__link:hover .icon-svg {
  stroke: #fff;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.tag {
  border-radius: 24px;
  border: 1px solid var(--bg-light-blue);
  display: flex;
  padding: 4px 12px;
  align-items: center;
  gap: 4px;
  font-size: 0.875rem;
  line-height: 1.3;
}
.tag__icon {
  width: 24px;
  height: 24px;
  stroke: var(--text-blue);
}

.section-map {
  background-color: var(--bg-light-blue);
}
.section-map__title-link {
  color: #36568D;
  border-bottom: 1px solid rgba(37, 67, 117, 0.25);
}
.section-map .title__border {
  border-color: #BFC9D2;
}

.map {
  max-height: 779px;
  position: relative;
}
.map__image {
  width: 100%;
  max-width: 1342px;
  aspect-ratio: 1342/779;
}
.map__marker {
  --color: #D08935;
  position: absolute;
  width: 162px;
  height: 162px;
  background-color: var(--color);
  color: #fff;
  font-size: 1.125rem;
  font-weight: bold;
  line-height: 1.3;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s;
}
.map__marker::before, .map__marker::after {
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid var(--color);
  border-radius: 50%;
  transition: all 0.2s;
}
.map__marker::before {
  opacity: 0.75;
  width: 192px;
  height: 192px;
}
.map__marker::after {
  opacity: 0.25;
  width: 238px;
  height: 238px;
}
.map__marker:hover {
  --color: #fff;
  color: #D08935;
}
.map__marker--eu {
  top: 22%;
  left: 49%;
}
.map__marker--ch {
  top: 19%;
  right: 15%;
}
.map__marker--as {
  top: 39%;
  right: 21%;
}
.map__marker--af {
  bottom: 29%;
  left: 48%;
}

.services {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 48px;
  background-color: var(--bg-light-blue);
}
.services__list {
  padding: 48px 48px 48px 27px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  position: relative;
}
.services__list-controls {
  position: absolute;
  top: 48px;
  bottom: 48px;
  left: -2px;
  width: 2px;
  background-color: #fff;
}
.services__list-item {
  position: relative;
  display: flex;
  gap: 24px;
  background-color: rgba(255, 255, 255, 0.5);
  padding: 24px;
  font-size: 1.125rem;
  line-height: 1.3;
  color: var(--text-grey);
}
.services__list-item:hover {
  cursor: pointer;
  background-color: #fff;
}
.services__list-item::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -32px;
  width: 8px;
  height: 8px;
  border-radius: 100%;
  background-color: #fff;
  box-shadow: 0 0 0 4px var(--bg-light-blue);
}
.services__list-item-icon {
  width: 40px;
  height: 40px;
  background-position: center center;
}
.services__list-item-icon-1 {
  background-image: url(./../images/services-icon-1.svg);
}
.services__list-item-icon-2 {
  background-image: url(./../images/services-icon-2.svg);
}
.services__list-item-icon-3 {
  background-image: url(./../images/services-icon-3.svg);
}
.services__list-item-icon-4 {
  background-image: url(./../images/services-icon-4.svg);
}
.services__list-item-icon-5 {
  background-image: url(./../images/services-icon-5.svg);
}

.services__list-item.is-active {
  background-color: #fff;
}
.services__list-item.is-active .services__list-item-icon-1 {
  background-image: url(./../images/services-icon-1-active.svg);
}
.services__list-item.is-active .services__list-item-icon-2 {
  background-image: url(./../images/services-icon-2-active.svg);
}
.services__list-item.is-active .services__list-item-icon-3 {
  background-image: url(./../images/services-icon-3-active.svg);
}
.services__list-item.is-active .services__list-item-icon-4 {
  background-image: url(./../images/services-icon-4-active.svg);
}
.services__list-item.is-active .services__list-item-icon-5 {
  background-image: url(./../images/services-icon-5-active.svg);
}
.services__list-item.is-active::before {
  background-color: var(--bg-blue);
}
.services__list-item.is-active::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: -29px;
  width: 2px;
  height: calc(100% + 24px);
  border-radius: 0 0 8px 8px;
  background-color: var(--bg-blue);
}

.section-advantages {
  background-color: var(--bg-blue);
}
.section-advantages .section__wrapper {
  position: relative;
}
.section-advantages__title-box {
  border: 1px solid rgba(255, 255, 255, 0.15);
  padding: 24px;
  color: #fff;
  position: absolute;
  width: calc(50% - 12px);
  height: calc(33% - 12px);
  left: 0;
}
.section-advantages__title-box .title {
  max-width: none;
  font-size: 2.5rem;
}
.section-advantages__title-box .subtitle__icon {
  color: #fff;
}

.advantages {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  position: relative;
}

.advantage {
  padding: 24px;
  background-color: #fff;
  display: flex;
}
.advantage__left {
  position: relative;
  flex: 1 0 250px;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: 1px solid var(--bg-light-blue);
}
.advantage__right {
  padding-left: 24px;
}
.advantage__num {
  position: absolute;
  top: 0;
  left: 0;
  width: 32px;
  height: 32px;
  border-radius: 100%;
  border: 1px solid var(--text-blue);
  text-align: center;
  line-height: 30px;
}
.advantage__icon {
  width: 100px;
  height: 100px;
}
.advantage__title {
  font-size: 1.5rem;
  font-weight: bold;
  line-height: 1.3;
  margin-bottom: 12px;
}
.advantage__description {
  line-height: 1.3;
}

.section-clients {
  background-color: var(--bg-light-blue);
}
.section-clients .title__border {
  border-color: #BFC9D2;
  align-items: flex-end;
  padding-bottom: 32px;
}
.section-clients .subtitle {
  margin-bottom: 0;
}

.clients {
  display: flex;
  gap: 24px;
}
.clients__item {
  background-color: #fff;
  align-items: center;
  justify-content: center;
}

.footer {
  padding: 80px 0 var(--template-padding);
  background: var(--bg-black);
  color: #fff;
  z-index: 0;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}
.footer .title__border {
  border-color: rgba(255, 255, 255, 0.25);
}
.footer .title__border .btn {
  border-color: rgba(255, 255, 255, 0.25);
}
.footer .title {
  padding-bottom: 60px;
}
.footer__body {
  padding: 24px;
  color: var(--text-black);
  background-color: #fff;
}
.footer__copyright-wrapper {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid var(--bg-light-blue);
  padding-top: 24px;
  margin-top: 80px;
  color: var(--text-grey);
  font-size: 0.875rem;
  line-height: 1.3;
}
.footer__copyright-wrapper button {
  appearance: none;
}
.footer__logo {
  width: 128px;
  height: 48px;
}
.footer__body-content {
  display: flex;
  justify-content: space-between;
}
.footer__nav-box {
  max-width: 864px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 40px;
  column-gap: 138px;
}
.footer__nav-column {
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.footer__nav-column:last-child {
  grid-column: 1/3;
}
.footer__nav-title {
  font-size: 0.875rem;
  color: var(--text-grey);
  padding-bottom: 16px;
  margin-bottom: 4px;
  border-bottom: 1px solid var(--bg-light-blue);
}
.footer__nav-link {
  line-height: 1.3;
  align-self: flex-start;
}

.backdrop {
  position: fixed;
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(29, 35, 44, 0.25);
  backdrop-filter: blur(30px);
  z-index: 2;
  display: none;
  transition: opacity 0.1s;
  opacity: 0;
}
.backdrop.is-active {
  opacity: 1;
}

.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(29, 35, 44, 0.25);
  backdrop-filter: blur(30px);
  z-index: 4;
  opacity: 0;
  transition: opacity 0.3s;
  justify-content: flex-end;
}
.popup.is-shown {
  opacity: 1;
}
.popup-window {
  position: relative;
  width: 50%;
  background-color: #fff;
  padding: var(--template-padding);
}
.popup__close {
  position: absolute;
  left: -24px;
  top: 48px;
  transform: translateX(-100%);
  z-index: 4;
}
.popup__close .btn {
  padding: 14px 38px;
  border: none;
}
.popup__close .icon-svg {
  width: 24px;
  height: 24px;
}

@media (max-width: 1279px) {
  :root {
    --template-padding: 24px;
    --template-max-width: 1279px;
  }
  h1 {
    font-size: 4.25rem;
  }
  .header {
    padding-top: 24px;
  }
  .header__main-left, .header__main-right {
    width: 150px;
  }
  .header__contacts {
    display: none;
  }
  .header .btn-language {
    display: none;
  }
  .header__expanded {
    height: 100vh;
    padding-top: 80px;
    flex-direction: column;
  }
  .header__expanded-nav {
    flex: 1 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 24px;
  }
  .header__copyright-wrapper {
    flex: 0 0 auto;
    margin-top: 0;
  }
  .header__nav-column:last-child {
    grid-column: 1/3;
  }
  .header.is-fixed .header__expanded {
    display: flex;
    height: calc(100vh - 52px - 48px);
  }
  .section {
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .map__marker {
    width: 108px;
    height: 108px;
  }
  .map__marker::before {
    width: 128px;
    height: 128px;
  }
  .map__marker::after {
    width: 159px;
    height: 159px;
  }
  .map__marker--eu {
    top: 12%;
    left: 49%;
  }
  .map__marker--ch {
    top: 28%;
    right: 7%;
  }
  .map__marker--as {
    top: 36%;
    right: 24%;
  }
  .map__marker--af {
    bottom: 23%;
    left: 42%;
  }
  .services {
    grid-template-columns: 100%;
    grid-template-rows: auto auto;
    gap: 0;
  }
  .services__list {
    padding-left: 72px;
  }
  .services__list-controls {
    left: 43px;
  }
  .section-advantages__title-box {
    margin-bottom: 24px;
    position: relative;
    left: unset;
    top: unset;
    width: 100%;
    height: auto;
  }
  .section-advantages .section__wrapper {
    overflow: hidden;
  }
  .advantages {
    display: flex;
    overflow: hidden;
  }
  .advantage {
    flex: 1 0 672px;
  }
  .advantage--empty {
    display: none;
  }
  .clients {
    overflow: hidden;
  }
  .clients__item {
    flex: 0 0 250px;
  }
  .footer .title {
    font-size: 2.5rem;
    padding-bottom: 32px;
  }
  .footer__body-content {
    flex-direction: column;
    gap: 32px;
  }
  .popup {
    justify-content: center;
    align-items: flex-end;
  }
  .popup__close {
    left: 50%;
    top: -16px;
    transform: translate(-50%, -100%);
  }
  .popup-window {
    width: 100%;
    height: calc(100vh - 84px);
  }
}